/* Hero Section - Refined Sleek Design */
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: linear-gradient(135deg, #FFFEF7 0%, #FAF8F1 100%);
  overflow: hidden;
  padding: 8rem 0 4rem;
}

/* Background Elements */
.backgroundElements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

.gradientOrb1 {
  position: absolute;
  top: 10%;
  right: 15%;
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(210, 105, 30, 0.12) 0%, rgba(255, 140, 0, 0.08) 50%, transparent 70%);
  border-radius: 50%;
  filter: blur(40px);
}

.gradientOrb2 {
  position: absolute;
  bottom: 20%;
  left: 10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(34, 139, 34, 0.10) 0%, rgba(50, 205, 50, 0.06) 50%, transparent 70%);
  border-radius: 50%;
  filter: blur(35px);
}

/* Hero Content */
.heroContent {
  position: relative;
  z-index: 2;
  max-width: 600px;
  text-align: center;
  margin: 0 auto 4rem;
}

/* Badge */
.badgeContainer {
  margin-bottom: 2rem;
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: rgba(210, 105, 30, 0.1);
  border: 1px solid rgba(210, 105, 30, 0.2);
  border-radius: 50px;
  color: #D2691E;
  font-size: 0.9rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

.badgeIcon {
  font-size: 1.2rem;
}

/* Hero Title */
.heroTitle {
  font-size: 4.5rem;
  font-weight: 800;
  line-height: 1.1;
  color: #2F2F2F;
  margin-bottom: 1.5rem;
  letter-spacing: -0.02em;
}

.highlightText {
  background: linear-gradient(135deg, #D2691E 0%, #FF8C00 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Hero Subtitle */
.heroSubtitle {
  font-size: 1.3rem;
  line-height: 1.6;
  color: #555555;
  margin-bottom: 3rem;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
}

/* Hero Actions */
.heroActions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
  flex-wrap: wrap;
}

.primaryBtn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: linear-gradient(135deg, #D2691E 0%, #FF8C00 100%);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.1rem;
  box-shadow: 0 8px 25px rgba(210, 105, 30, 0.25);
  transition: all 0.3s ease;
}

.primaryBtn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(210, 105, 30, 0.35);
}

.primaryBtn svg {
  transition: transform 0.3s ease;
}

.primaryBtn:hover svg {
  transform: translateX(4px);
}

.secondaryBtn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: white;
  color: #2F2F2F;
  text-decoration: none;
  border: 2px solid #F0EDE6;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.1rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.secondaryBtn:hover {
  background: #FFFEF7;
  border-color: #E6E1D8;
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

/* Trust Indicators */
.trustIndicators {
  text-align: center;
}

.statsRow {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
  padding: 2rem 0;
  border-top: 1px solid rgba(240, 237, 230, 0.8);
  border-bottom: 1px solid rgba(240, 237, 230, 0.8);
}

.statItem {
  text-align: center;
}

.statNumber {
  font-size: 2.5rem;
  font-weight: 800;
  color: #D2691E;
  line-height: 1;
  margin-bottom: 0.25rem;
}

.statLabel {
  font-size: 0.9rem;
  color: #8B8377;
  font-weight: 500;
}

.statDivider {
  width: 1px;
  height: 40px;
  background: rgba(240, 237, 230, 0.8);
}

.companiesText {
  font-size: 0.9rem;
  color: #8B8377;
  font-weight: 500;
}

/* Hero Visual */
.heroVisual {
  position: relative;
  max-width: 500px;
  margin: 0 auto;
}

.visualCard {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(210, 105, 30, 0.15);
}

.heroImage {
  width: 100%;
  height: 400px;
  object-fit: cover;
  display: block;
}

.floatingBadge {
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
  color: #2F2F2F;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.liveDot {
  width: 8px;
  height: 8px;
  background: #228B22;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .heroTitle {
    font-size: 4rem;
  }
  
  .heroSubtitle {
    font-size: 1.2rem;
  }
}

@media (max-width: 968px) {
  .hero {
    padding: 6rem 0 3rem;
  }
  
  .heroTitle {
    font-size: 3.5rem;
  }
  
  .heroSubtitle {
    font-size: 1.1rem;
  }
  
  .statsRow {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .statDivider {
    width: 40px;
    height: 1px;
  }
  
  .heroActions {
    flex-direction: column;
    gap: 1rem;
  }
  
  .primaryBtn,
  .secondaryBtn {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }
  
  .gradientOrb1 {
    width: 300px;
    height: 300px;
    top: 5%;
    right: 5%;
  }
  
  .gradientOrb2 {
    width: 250px;
    height: 250px;
    bottom: 10%;
    left: 5%;
  }
}

@media (max-width: 768px) {
  .hero {
    padding: 5rem 0 2rem;
  }
  
  .heroTitle {
    font-size: 2.8rem;
  }
  
  .heroSubtitle {
    font-size: 1rem;
    margin-bottom: 2.5rem;
  }
  
  .heroActions {
    margin-bottom: 3rem;
  }
  
  .primaryBtn,
  .secondaryBtn {
    padding: 1rem 2rem;
    font-size: 1rem;
  }
  
  .heroVisual {
    max-width: 400px;
  }
  
  .heroImage {
    height: 300px;
  }
  
  .badge {
    padding: 0.875rem 1.75rem;
    font-size: 0.85rem;
  }
  
  .statsRow {
    padding: 1.5rem 0;
  }
  
  .statNumber {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .heroTitle {
    font-size: 2.2rem;
  }
  
  .heroSubtitle {
    font-size: 0.95rem;
  }
  
  .heroVisual {
    max-width: 350px;
  }
  
  .heroImage {
    height: 250px;
  }
  
  .statNumber {
    font-size: 1.75rem;
  }
  
  .floatingBadge {
    bottom: 1rem;
    left: 1rem;
    padding: 0.625rem 1rem;
    font-size: 0.8rem;
  }
}

/* Animation Keyframes */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
} 