/* Header - Professional Navigation */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 254, 247, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(240, 237, 230, 0.8);
  z-index: 1000;
  transition: all 0.3s ease;
}

.header.scrolled {
  background: rgba(255, 254, 247, 0.98);
  box-shadow: 0 4px 20px rgba(210, 105, 30, 0.08);
}

.headerContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 2rem;
  padding-right: 2rem;
}

/* Logo */
.logoLink {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: transform 0.3s ease;
}

.logoLink:hover {
  transform: translateY(-1px);
}

/* Navigation */
.nav {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}

.navItem {
  position: relative;
}

.navLink {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #2F2F2F;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.95rem;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.navLink:hover {
  color: #D2691E;
  background: rgba(210, 105, 30, 0.05);
}

.navLink.active {
  color: #D2691E;
  background: rgba(210, 105, 30, 0.1);
}

/* Header Actions */
.headerActions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.loginBtn {
  padding: 0.75rem 1.5rem;
  background: transparent;
  color: #2F2F2F;
  text-decoration: none;
  border: 1px solid rgba(240, 237, 230, 0.8);
  border-radius: 8px;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.loginBtn:hover {
  background: rgba(240, 237, 230, 0.5);
  border-color: rgba(210, 105, 30, 0.3);
  color: #D2691E;
}

.signupBtn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #D2691E 0%, #FF8C00 100%);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
  box-shadow: 0 4px 15px rgba(210, 105, 30, 0.2);
  transition: all 0.3s ease;
}

.signupBtn:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(210, 105, 30, 0.3);
}

.signupBtn svg {
  transition: transform 0.3s ease;
}

.signupBtn:hover svg {
  transform: translateX(2px);
}

/* Mobile Menu */
.mobileMenuBtn {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  color: #2F2F2F;
  transition: color 0.3s ease;
}

.mobileMenuBtn:hover {
  color: #D2691E;
}

.mobileNav {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(255, 254, 247, 0.98);
  backdrop-filter: blur(15px);
  border-bottom: 1px solid rgba(240, 237, 230, 0.8);
  box-shadow: 0 8px 25px rgba(210, 105, 30, 0.1);
  padding: 1.5rem 2rem;
}

.mobileNav.open {
  display: block;
}

.mobileNavList {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.mobileNavLink {
  color: #2F2F2F;
  text-decoration: none;
  font-weight: 500;
  font-size: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(240, 237, 230, 0.5);
  transition: color 0.3s ease;
}

.mobileNavLink:hover,
.mobileNavLink.active {
  color: #D2691E;
}

.mobileNavActions {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.mobileNavActions .loginBtn,
.mobileNavActions .signupBtn {
  width: 100%;
  text-align: center;
  justify-content: center;
}

/* Dropdown Menus */
.dropdown {
  position: relative;
}

.dropdownContent {
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  border: 1px solid rgba(240, 237, 230, 0.8);
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(210, 105, 30, 0.15);
  padding: 1rem 0;
  min-width: 200px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.3s ease;
  z-index: 1001;
}

.dropdown:hover .dropdownContent {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.dropdownItem {
  display: block;
  padding: 0.75rem 1.5rem;
  color: #2F2F2F;
  text-decoration: none;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.dropdownItem:hover {
  background: rgba(210, 105, 30, 0.05);
  color: #D2691E;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .headerContainer {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  
  .nav {
    gap: 2rem;
  }
}

@media (max-width: 968px) {
  .headerContainer {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .nav {
    display: none;
  }
  
  .headerActions {
    display: none;
  }
  
  .mobileMenuBtn {
    display: block;
  }
}

@media (max-width: 768px) {
  .headerContainer {
    padding: 0.75rem 1rem;
  }
  
  .mobileNav {
    padding: 1rem;
  }
  
  .mobileNavList {
    gap: 0.5rem;
  }
  
  .mobileNavLink {
    padding: 0.75rem 0;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .headerContainer {
    padding: 0.5rem 0.75rem;
  }
  
  .mobileNav {
    padding: 0.75rem;
  }
  
  .mobileNavActions {
    gap: 0.75rem;
  }
  
  .mobileNavActions .loginBtn,
  .mobileNavActions .signupBtn {
    font-size: 0.9rem;
    padding: 0.875rem 1.5rem;
  }
} 