/* Features Section - African-Inspired Professional Design */
.features {
  padding: 8rem 0;
  background: linear-gradient(180deg, #FAF7F2 0%, #F5F2ED 100%);
  position: relative;
  overflow: hidden;
}

/* Features Header */
.featuresHeader {
  text-align: center;
  margin-bottom: 5rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.sectionBadge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: rgba(198, 93, 0, 0.1);
  border: 1px solid rgba(198, 93, 0, 0.2);
  border-radius: 50px;
  color: #C65D00;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 2rem;
  backdrop-filter: blur(10px);
}

.badgeIcon {
  font-size: 1.1rem;
}

.featuresTitle {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.1;
  color: #2A2A2A;
  margin-bottom: 1.5rem;
  letter-spacing: -0.02em;
}

.highlightText {
  background: linear-gradient(135deg, #C65D00 0%, #F4A261 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.featuresSubtitle {
  font-size: 1.25rem;
  line-height: 1.6;
  color: #5A5A5A;
  margin: 0;
}

/* Features Grid */
.featuresGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 6rem;
}

.featureCard {
  background: white;
  border-radius: 20px;
  padding: 2.5rem;
  border: 1px solid rgba(229, 224, 216, 0.5);
  box-shadow: 0 4px 20px rgba(198, 93, 0, 0.05);
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.featureCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #C65D00 0%, #F4A261 100%);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.featureCard:hover::before {
  transform: scaleX(1);
}

.featureCard:hover {
  border-color: rgba(198, 93, 0, 0.2);
  box-shadow: 0 12px 40px rgba(198, 93, 0, 0.15);
}

/* Feature Icon */
.featureIcon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #C65D00 0%, #F4A261 100%);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  color: white;
  box-shadow: 0 8px 25px rgba(198, 93, 0, 0.25);
}

.featureIcon svg {
  width: 28px;
  height: 28px;
}

/* Feature Content */
.featureContent {
  flex: 1;
}

.featureTitle {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2A2A2A;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.featureDescription {
  font-size: 1rem;
  line-height: 1.6;
  color: #5A5A5A;
  margin: 0;
}

/* Stats Section */
.statsSection {
  background: linear-gradient(135deg, #2D5016 0%, #4A7C59 100%);
  border-radius: 24px;
  padding: 4rem 3rem;
  margin-bottom: 6rem;
  position: relative;
  overflow: hidden;
}

.statsContent {
  text-align: center;
  color: white;
}

.statsTitle {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  color: white;
}

.statsSubtitle {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.statsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.statItem {
  text-align: center;
}

.statNumber {
  font-size: 3rem;
  font-weight: 800;
  color: #F4A261;
  line-height: 1;
  margin-bottom: 0.5rem;
}

.statLabel {
  font-size: 1rem;
  opacity: 0.9;
  font-weight: 500;
}

/* CTA Section */
.ctaSection {
  background: linear-gradient(135deg, #2A2A2A 0%, #1A1A1A 100%);
  border-radius: 24px;
  padding: 4rem 3rem;
  position: relative;
  overflow: hidden;
  text-align: center;
}

.ctaBackground {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.ctaOrb1 {
  position: absolute;
  top: -20%;
  right: -10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgba(198, 93, 0, 0.15) 0%, rgba(244, 162, 97, 0.1) 50%, transparent 70%);
  border-radius: 50%;
  filter: blur(40px);
}

.ctaOrb2 {
  position: absolute;
  bottom: -20%;
  left: -10%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgba(45, 80, 22, 0.12) 0%, rgba(74, 124, 89, 0.08) 50%, transparent 70%);
  border-radius: 50%;
  filter: blur(35px);
}

.ctaContent {
  position: relative;
  z-index: 2;
  color: white;
}

.ctaTitle {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  color: white;
}

.ctaSubtitle {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 2.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.ctaActions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.ctaPrimary {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: linear-gradient(135deg, #C65D00 0%, #F4A261 100%);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.1rem;
  box-shadow: 0 8px 25px rgba(198, 93, 0, 0.25);
  transition: all 0.3s ease;
  border: none;
}

.ctaPrimary:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(198, 93, 0, 0.35);
}

.ctaPrimary svg {
  transition: transform 0.3s ease;
}

.ctaPrimary:hover svg {
  transform: translateX(4px);
}

.ctaSecondary {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: transparent;
  color: white;
  text-decoration: none;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.ctaSecondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .featuresGrid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 1.5rem;
  }
  
  .featuresTitle {
    font-size: 3rem;
  }
  
  .statsSection,
  .ctaSection {
    padding: 3rem 2rem;
  }
}

@media (max-width: 968px) {
  .features {
    padding: 6rem 0;
  }
  
  .featuresHeader {
    margin-bottom: 4rem;
  }
  
  .featuresTitle {
    font-size: 2.5rem;
  }
  
  .featuresSubtitle {
    font-size: 1.1rem;
  }
  
  .featuresGrid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    margin-bottom: 4rem;
  }
  
  .featureCard {
    padding: 2rem;
  }
  
  .statsGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  
  .statsTitle,
  .ctaTitle {
    font-size: 2rem;
  }
  
  .ctaActions {
    flex-direction: column;
    align-items: center;
  }
  
  .ctaPrimary,
  .ctaSecondary {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .features {
    padding: 4rem 0;
  }
  
  .featuresHeader {
    margin-bottom: 3rem;
  }
  
  .featuresTitle {
    font-size: 2rem;
  }
  
  .featuresSubtitle {
    font-size: 1rem;
  }
  
  .featureCard {
    padding: 1.5rem;
  }
  
  .featureIcon {
    width: 50px;
    height: 50px;
    margin-bottom: 1rem;
  }
  
  .featureIcon svg {
    width: 24px;
    height: 24px;
  }
  
  .featureTitle {
    font-size: 1.3rem;
  }
  
  .featureDescription {
    font-size: 0.95rem;
  }
  
  .statsSection,
  .ctaSection {
    padding: 2.5rem 1.5rem;
    margin-bottom: 3rem;
  }
  
  .statsGrid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .statNumber {
    font-size: 2.5rem;
  }
  
  .statsTitle,
  .ctaTitle {
    font-size: 1.75rem;
  }
  
  .statsSubtitle,
  .ctaSubtitle {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .featuresTitle {
    font-size: 1.75rem;
  }
  
  .featureCard {
    padding: 1.25rem;
  }
  
  .featureIcon {
    width: 45px;
    height: 45px;
  }
  
  .featureIcon svg {
    width: 20px;
    height: 20px;
  }
  
  .featureTitle {
    font-size: 1.2rem;
  }
  
  .featureDescription {
    font-size: 0.9rem;
  }
  
  .statsSection,
  .ctaSection {
    padding: 2rem 1rem;
  }
  
  .statNumber {
    font-size: 2rem;
  }
  
  .statsTitle,
  .ctaTitle {
    font-size: 1.5rem;
  }
  
  .ctaPrimary,
  .ctaSecondary {
    padding: 1rem 2rem;
    font-size: 1rem;
  }
} 